@import 'src/styles/shared.scss';

.slots-wrapper {
    display: flex;
    flex-wrap: wrap;
    padding: $unit * 10;

    .slot {
        $slotWidth: $unit * 54;

        flex-shrink: 0;
        position: relative;
        width: $slotWidth;
        height: $slotWidth;
        background-color: $default-panel-bg;
        margin-right: $unit * 10;
        margin-top: $unit * 10;

        .slot-key {
            position: absolute;
            color: $secondary-text-color;
            top: $unit * 2;
            left: $unit * 4;
        }

        .slot-link {
            position: relative;
            width: 100%;
            height: 100%;

            .slot-link-name {
                position: absolute;
                font-size: $unit * 12;
                color: $secondary-text-color;
                bottom: $unit * 6;
                left: $unit * 4;
                width: 70%;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
    }
}